*,*:after,*:before {
    -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;

}

body {
    font-family: arial;
    font-size: 16px;
    margin: 0;
    background: #fff;
    background: linear-gradient(to right bottom, #d13cff,#031f6a);
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    color: #000;
}

.voice_to_text {
    width: 600px;
    text-align: center;
}

#convert_text {
    width: 100%;
    height: 200px;
    border-radius: 10px;
    resize: none;
    padding: 10px;
    font-size: 20px;
    margin-bottom: 10px;
    z-index: 999;
    color: black;
    background-color: white;

}

h1 {
    font-size: 50px;
    color: #fff;
}

button {

    /* display: flex; */
    /* flex-direction: row; */
    margin-top: 10px;
    align-items: flex-start;
    padding: 12px 20px;
    background: #0ea4da;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
   
}